#app-header ul {
  display: flex;
  margin: 0 0 10px 0;
  padding: 0;
  list-style: none; }
  #app-header ul li {
    background-color: gray;
    width: 50%;
    margin-right: 5px;
    text-align: center;
    cursor: pointer;
    padding: 3px 0;
    font-size: .9em;
    transition: all .3s;
    border-radius: 6px; }
    #app-header ul li.selected {
      background-color: whitesmoke; }
    #app-header ul li:hover {
      background-color: lightgray; }
    #app-header ul li:active {
      background-color: whitesmoke; }
    #app-header ul li:last-child {
      margin-right: 0; }
