.keyword-option {
  padding: 2px 5px;
  border-radius: 10px;
  font-size: .9em;
  text-align: left;
  background-color: whitesmoke;
  margin-bottom: 5px;
  cursor: pointer;
  transition: all .3s; }
  .keyword-option:last-child {
    margin-bottom: 0; }
  .keyword-option.not-found {
    background-color: transparent !important;
    cursor: auto !important;
    text-align: center;
    font-size: 14px;
    color: white; }
    .keyword-option.not-found span {
      cursor: auto !important; }
  .keyword-option:hover {
    background-color: wheat; }
  .keyword-option span {
    cursor: pointer; }
    .keyword-option span img {
      width: 50px; }
